body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(119, 128, 248), rgb(0, 14, 87));

}
h2, p {
    font-family: 'Roboto Mono', monospace;
}

.card:hover {
    cursor: pointer;
}

div input {
    font-family: 'Roboto Mono', monospace;
}
/* #### Generated By: http://www.cufonfonts.com #### */

